<template>

    <div class="c-rkw-bookmark-modal" :class="{'c-rkw-bookmark-modal--open': open === true}">
        <div class="c-rkw-bookmark-modal__wrapper">
            <div class="o-container">
                <a href="#" class="logo"></a>
                <a href="#" class="c-rkw-bookmark-modal__close" @click.prevent="toggleBookmarkModal"></a>
                <header class="c-rkw-bookmark-modal__header">
                    <h2 class="c-rkw-bookmark-modal__headline">
                        Beispiele Merkliste
                        <span v-if="entries.length">({{entries.length}})</span>
                    </h2>
                    <div class="c-rkw-bookmark-modal__share-container">
                        <span :class="['c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--link', {'c-rkw-bookmark-modal__share-item--copied': copied === true}]" @click.prevent="copyLink()"></span>
                    </div>
                    <div class="c-rkw-bookmark-modal__share-container c-rkw-bookmark-modal__share-container--bottom">
                        <span class="c-rkw-bookmark-modal__share-label">Teilen: </span>
                        <span class="c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--xing"></span>
                        <span class="c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--facebook"></span>
                        <span class="c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--twitter"></span>
                        <span class="c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--linkedin"></span>
                        <span class="c-rkw-bookmark-modal__share-item c-rkw-bookmark-modal__share-item--email"></span>
                    </div>
                </header>
                <div class="o-grid">
                    <transition name="fade">
                        <div v-if="!entries.length" class="o-grid__col u-12/12">
                            <p class="c-rkw-bookmark-modal__empty-info">
                                Leider haben Sie noch keine Daten in Ihrer Merkliste!
                            </p>
                        </div>
                    </transition>

                    <div v-if="entries.length" class="o-grid__col u-12/12">
                        <div v-for="(_entries, _modelID, index) in pEntries" class="c-rkw-bookmark-modal__group" v-if="_entries.length">
                            <div class="o-grid">
                                <h3 :class="`c-rkw-bookmark-modal__group-header c-rkw-bookmark-modal__group-header${models[_modelID].color} o-grid__col u-12/12`">
                                    {{models[_modelID].title}}
                                </h3>
                            </div>

                            <div class="c-rkw-bookmark-modal__group-items o-grid">
                                <div v-for="entry in _entries" class="o-grid__col u-3/12" v-if="getContentModel(entry).id === models[_modelID].id">
                                    <transition name="fade" v-on:after-leave="afterLeave">
                                        <div v-if="entry !== removing" :class="`c-rkw-bookmark-modal__bookmark c-rkw-bookmark-modal__bookmark${getContentColor(entry)}`" :style="`background-image: url(${settings.pageUrl}${getBookmarkContent(entry).sliderImages[0][1]});`" @click.prevent="openContentModal(entry)">
                                            <span class="c-rkw-bookmark-modal__bookmark-remove" v-on:click.stop="removeQueue(entry)"></span>
                                            <span class="c-rkw-bookmark-modal__bookmark-text">
                                                <strong>{{getBookmarkContent(entry).content.header}}</strong><br><br>
                                                {{getBookmarkContent(entry).content.subheader}}
                                            </span>
                                        </div>
                                    </transition>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>

    import mixins from "../mixins"

    export default {
        data () {
            return {
                removing: null,
                copied: false
            }
        },
        computed: {
            settings () {
                return this.$store.state.settings
            },
            open () {
                return this.$store.state.runtime.bookmarks.open
            },
            entries () {
                return this.$store.state.runtime.bookmarks.entries
            },
            pEntries () {
                let obj = {}
                for(let modelKey in this.models) {
                    if(this.models.hasOwnProperty(modelKey)) {
                        obj[modelKey] = []
                    }
                }
                for (let i = 0; i < this.entries.length; i++) {
                    let model = this.getContentModel(this.entries[i])
                    obj['uid' + model.id].push(this.entries[i])
                }
                return obj
            },
            models () {
                return this.$store.state.data.models
            }
        },
        beforeMount: function() {
            if(this.$route.params.hasOwnProperty('bookmarks')) {
                if(typeof JSON.parse(this.$route.params.bookmarks) === "object") {
                    let storage = window.localStorage
                    storage.setItem('rkw-digikit_bookmarks', this.$route.params.bookmarks)
                }
            }


            let storage = window.localStorage
            let bookmarks = storage.getItem('rkw-digikit_bookmarks')
            if(bookmarks !== null && typeof JSON.parse(bookmarks) === "object") {
                this.$store.commit('updateBookmarks', bookmarks)
            }
        },
        beforeCreate () {

        },
        watch: {
            '$store.state.runtime.bookmarks.entries': function(val, old) {
                let storage = window.localStorage
                storage.setItem('rkw-digikit_bookmarks', JSON.stringify(this.entries))
            }
        },
        mixins: [mixins],
        methods: {
            openContentModal (uid) {
                this.$store.commit('closeBookmarkModal')
                this.$store.commit('setActiveInstance', uid)
                router.push({path: '/instance/' + uid})
            },
            getBookmarkContent (uid) {
                return this.$store.state.data['instances'][uid]
            },
            removeQueue (uid) {
                this.removing = uid
            },
            afterLeave () {
                this.removeBookmark(this.removing)
                this.removing = null
            },
            copyLink () {
                let e = encodeURIComponent(JSON.stringify(this.entries))
                let uri = global.host.uri + '#/bookmarks/' + e
                this.clipboard(uri)
                this.copied = true
            },
            clipboard(text) {
                if (window.clipboardData && window.clipboardData.setData) {
                    // IE specific code path to prevent textarea being shown while dialog is visible.
                    return clipboardData.setData("Text", text);

                } else if (document.queryCommandSupported && document.queryCommandSupported("copy")) {
                    var textarea = document.createElement("textarea");
                    textarea.textContent = text;
                    textarea.style.position = "fixed";  // Prevent scrolling to bottom of page in MS Edge.
                    document.body.appendChild(textarea);
                    textarea.select();
                    try {
                        return document.execCommand("copy");  // Security exception may be thrown by some browsers.
                    } catch (ex) {
                        console.warn("Copy to clipboard failed.", ex);
                        return false;
                    } finally {
                        document.body.removeChild(textarea);
                    }
                }
            }
        }
    }
</script>


